<template>
  <div class="index-container" style="padding-top: 0">
    <div  style="margin-bottom: 50px">
      <div class="mdui-card post-card">
        <div style="min-height: 400px">
          <div id="preview" style="margin: 80px"></div>
        </div>
      </div>
    </div>
    <div >
      <!--<Introduce></Introduce>-->
      <div id="outline" class="mdui-card mdui-hoverable sidebar-module">
      </div>
    </div>
  </div>
</template>
<script>

import Vditor from "vditor";
import 'vditor/dist/index.css'
import {getSession} from "../utils/SessionStorage";

export default {
  data() {
    return {

    }
  },
  mounted() {
    this.getAbout()
  },
  methods: {
    renderMarkdown(md) {
      Vditor.preview(document.getElementById('preview'), md, {
        hljs: {style: 'github'}
      })
    },
    getAbout(){
      this.renderMarkdown(getSession("about"))
    }
  },
}
</script>
